<template>
  <a-modal
    title="海报预览"
    :visible="visible"
    :width="800"
    @cancel="handleCancel"
    :footer="null"
  >
    <div class="poster-preview">
      <div 
        class="preview-content"
        :style="{
          width: `${canvasWidth}px`,
          height: `${canvasHeight}px`,
          transform: `scale(${scale})`
        }"
      >
        <div
          v-for="element in elements"
          :key="element.id"
          class="preview-element"
          :style="getElementStyle(element)"
        >
          <!-- 文本元素 -->
          <template v-if="element.type === 'text'">
            <div 
              class="text-content"
              :style="getTextStyle(element)"
            >
              {{ element.content }}
            </div>
          </template>

          <!-- 图片元素 -->
          <template v-if="element.type === 'image'">
            <img 
              :src="element.url"
              :style="getImageStyle(element)"
              draggable="false"
            >
          </template>

          <!-- 形状元素 -->
          <template v-if="element.type === 'shape'">
            <div 
              class="shape-content"
              :style="getShapeStyle(element)"
            ></div>
          </template>

          <!-- 二维码元素 -->
          <template v-if="element.type === 'qrcode'">
            <div class="qrcode-content">
              <qrcode
                :value="element.content"
                :size="Math.min(element.style.width, element.style.height)"
                level="H"
                :foreground="element.style.color || '#000000'"
                :background="element.style.backgroundColor || '#FFFFFF'"
              ></qrcode>
            </div>
          </template>
        </div>
      </div>

      <div class="preview-actions">
        <a-space>
          <a-button @click="handleDownload">
            <template #icon><download-outlined /></template>
            下载图片
          </a-button>
          <a-button type="primary" @click="handleShare">
            <template #icon><share-alt-outlined /></template>
            分享
          </a-button>
        </a-space>
      </div>
    </div>
  </a-modal>
</template>

<script>
import { DownloadOutlined, ShareAltOutlined } from '@ant-design/icons-vue';
import qrcode from 'qrcode.vue'
import html2canvas from 'html2canvas';

export default {
  name: 'PosterPreview',
  components: {
    DownloadOutlined,
    ShareAltOutlined,
    qrcode
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    elements: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      canvasWidth: 750,
      canvasHeight: 1334,
      scale: 1
    }
  },
  watch: {
    visible(val) {
      if (val) {
        this.$nextTick(() => {
          this.calculateScale();
        });
      }
    }
  },
  methods: {
    calculateScale() {
      const modalWidth = 800 - 48; // 减去内边距
      const modalHeight = window.innerHeight * 0.8 - 110; // 减去标题和按钮的高度
      const scaleX = modalWidth / this.canvasWidth;
      const scaleY = modalHeight / this.canvasHeight;
      this.scale = Math.min(scaleX, scaleY, 1);
    },

    getElementStyle(element) {
      return {
        position: 'absolute',
        left: `${element.style.left}px`,
        top: `${element.style.top}px`,
        width: `${element.style.width}px`,
        height: `${element.style.height}px`,
        transform: `rotate(${element.style.rotate || 0}deg)`,
        opacity: (element.style.opacity || 100) / 100
      }
    },

    getTextStyle(element) {
      return {
        fontFamily: element.style.fontFamily || 'Arial',
        fontSize: `${element.style.fontSize || 14}px`,
        color: element.style.color || '#000000',
        textAlign: element.style.textAlign || 'left',
        width: '100%',
        height: '100%',
        wordBreak: 'break-word',
        overflow: 'hidden'
      }
    },

    getImageStyle(element) {
      return {
        width: '100%',
        height: '100%',
        objectFit: element.style.objectFit || 'cover'
      }
    },

    getShapeStyle(element) {
      return {
        width: '100%',
        height: '100%',
        backgroundColor: element.style.backgroundColor || '#000000',
        borderRadius: element.style.borderRadius || '0'
      }
    },

    handleCancel() {
      this.$emit('update:visible', false);
    },

    async handleDownload() {
      try {
        const previewContent = this.$el.querySelector('.preview-content');
        const canvas = await html2canvas(previewContent, {
          scale: window.devicePixelRatio,
          useCORS: true,
          allowTaint: true,
          backgroundColor: '#ffffff'
        });

        const link = document.createElement('a');
        link.download = `poster-${Date.now()}.png`;
        link.href = canvas.toDataURL('image/png');
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      } catch (error) {
        console.error('下载图片失败：', error);
        this.$message.error('下载图片失败，请重试');
      }
    },

    handleShare() {
      // TODO: 实现分享功能
      this.$message.info('分享功能开发中');
    }
  }
}
</script>

<style lang="less" scoped>
.poster-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 0 24px;
  
  .preview-content {
    position: relative;
    background-color: #fff;
    transform-origin: top center;
  }

  .preview-element {
    position: absolute;
    user-select: none;

    .text-content {
      width: 100%;
      height: 100%;
    }

    img {
      pointer-events: none;
    }
  }

  .preview-actions {
    margin-top: 20px;
  }

  .qrcode-content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
  }
}
</style> 